.page-header {
  border-bottom: 1px solid $special-grey-border;

  @media (min-width: $tablet-width) {
    border-bottom: none;
    display: flex;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 24px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 110px;
    padding-right: 110px;
    padding-top: 53px;
    max-width: 1440px;
    margin: 0 auto;
  }
}

.page-header__logo--no-js {
  margin-right: 60px;
}

.page-header__logo-img {
  @media (min-width: $tablet-width) {
    width: 174px;
    height: 52px;
  }

  @media (min-width: $desktop-width) {
    width: 202px;
    height: 59px;
  }

  &:hover {
    opacity: 0.8;
  }

  :active {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-header__logo-img--index-page {
  cursor: default;

  &:hover {
    opacity: 1;
  }

  :active {
    opacity: 1;
  }

  &:active {
    opacity: 1;
  }
}

.page-header__logo-index {
  cursor: default;
}

.page-header__logo-text {
  @media (min-width: $tablet-width) {
    display: none;
  }
}
